        ],
      ],
      ifshow: false,
      ifshow1: false,
      ifshow2: false,
      ifshow3: false
    };
  },
  methods: {
    

    //1s
    showMenu(index) {
      if(index === 0) {
        this.ifshow = true;
        this.ifshow1 = false;
        this.ifshow2 = false;
        this.ifshow3 = false;
      }
      if(index === 1) {
        this.ifshow = false;
        this.ifshow1 = true;
        this.ifshow2 = false;
        this.ifshow3 = false;
      }
      if(index === 2) {
        this.ifshow = false;
        this.ifshow1 = false;
        this.ifshow2 = true;
        this.ifshow3 = false;
      }
      if(index === 3) {
        this.ifshow = false;
        this.ifshow1 = false;
        this.ifshow2 = false;
        this.ifshow3 = true;
      }
      //this.$refs.subMenuBox.style.top = "50px";
      this.$nextTick(() => { 
        this.$refs.subMenuBox.style.transition = "all 0.5s"; 
        this.$refs.subMenuBox.style.top = "55px"; 
         
     });
    },
    
    hideMenu() {
      //this.$refs.subMenuBox.style.top = "-250px";
      this.$nextTick(() => {  
        this.$refs.subMenuBox.style.transition = "all 0.5s"; 
        this.$refs.subMenuBox.style.top = "-250px"; 
      });
      
      },
  },
};
