<template>
    <div class="container"></div>
</template>

<style>
    .container{
        width: 90%;
        height: 90%;
    }
</style>

<script>
    import * as d3 from 'd3';
    export default {
        mounted(){
            d3.json("../out.json",function (res) {
                return res;
            }).then(function (data) {
                //
                var nodes = [];
                var edges = [];
                var maketypes = [];    //
                var textstrs = [];     //
                for(var i = 0;i<data.nodes.length;i++){
                    //node
                    nodes.push({
                        name:data.nodes[i].name,
                        shortname:data.nodes[i].shortname,
                        type:data.nodes[i].type,
                        version:data.nodes[i].version,
                        maketype:data.nodes[i].maketype,
                        links:0,  //
                    })
                    //textstrs
                    textstrs.push(["name:"+nodes[i].name,"shortname:"+nodes[i].shortname,
                                "type:"+nodes[i].type,"version"+nodes[i].version,
                            "maketype:"+nodes[i].maketype]);
                    //
                    if(maketypes.indexOf(nodes[i].maketype)==-1)
                        maketypes.push(nodes[i].maketype);
                }
                for(var i = 0;i<data.links.length;i++){
                    //edges
                    var s=-1,t=-1;
                    var flag = 0;    //
                    while(s<0||t<0){
                        if(nodes[flag].name == data.links[i].source)
                            s = flag;
                        if(nodes[flag].name == data.links[i].target)
                            t = flag;
                        flag++;
                    }
                    nodes[s].links++; nodes[t].links++;
                    edges.push({
                        source:s,
                        target:t,
                    })
                }
                var w=window.innerWidth;//
                var h=window.innerHeight;
                    w=w*0.98;
                    h=h*0.9;
                //
                var origincolor = "skyblue";
                var emphacolor = "blue";
                //
                var marge = {top:0,bottom:60,left:0,right:60};
                var svg =  d3.select(".container")
                    .append("svg")
                    .attr("preserveAspectRatio", "xMidYMid meet")
                    .attr("viewBox", "0 0 2000 2000")
                    .attr("width","100%")
                    .attr("height","100%")
                
                var width = 2000;
                var height = 2000;
                var g = svg.append("g").attr("transform","translate("+marge.left+","+marge.top+")");
                //
                var color = d3.scaleOrdinal(d3.schemeCategory10);
                //
                var drag = d3.drag()
                        .on("start",started)
                        .on("drag",dragged)
                        .on("end",ended);

                //
                var forceSimulation = d3.forceSimulation(nodes)
                    .force("charge",d3.forceManyBody())
                    .force("link",d3.forceLink(edges))
                    .force("center",d3.forceCenter());
                //
                forceSimulation.nodes(nodes).on("tick",ticked);
                //
                forceSimulation.force("link")
                    .links(edges)
                    .distance(function(d){
                        return 10;
                    })
                //
                forceSimulation.force("center")
                    .x(width/2)
                    .y(height/2);
                //
                var links = g.append("g")
                    .selectAll("line")
                    .data(edges)
                    .enter()
                    .append("line")
                    .attr("stroke",origincolor)
                    .attr("class",function(d){
                        return "links"+d.index;
                    })
                    .attr("stroke-width",1);
                //
                var gs = g.selectAll(".circleText")
                    .data(nodes)
                    .enter()
                    .append("g")
                    .attr("transform",function(d,i){
                        var cirX = d.x;
                        var cirY = d.y;
                        return "translate("+cirX+","+cirY+")";
                    })
                    .attr("class",function(d){return "gs"+d.index;})       
                const text = gs.append("text")
                    .attr("class",function(d){
                        return "text"+d.index;
                    })
                    .attr("x",20)
                    .attr("y",-20)
                    .attr("dy",10)
                    .attr("fill","green")
                    .attr("font-size",10)
                    .attr("visibility","hidden");   //
                //
                for(var i = 0;i<nodes.length;i++){
                    d3.select(".text"+i)
                        .selectAll("tspan")  //
                        .data(textstrs[i])
                        .enter()
                        .append("tspan")
                        .attr("x",text.attr("x"))
                        .attr("dy","1em")
                        .text(function(d){
                        return d;
                        });
                }
                const circle = gs.append("circle")
                    .attr("class",function(d){
                        return "circle"+d.index;
                    })
                    .attr("r",function(d){
                        //
                        return 5+d.links/50;
                    })
                    .attr("fill",function(d){
                        //
                        return color(maketypes.indexOf(d.maketype));
                    })
                    .style("opacity",0.5)
                    .on("mouseover",function(d){
                        //
                        //
                        d3.select(".text"+d.target.__data__.index).attr("visibility","visible");
                        for(var i = 0;i<edges.length;i++){
                            //
                            if(edges[i].source.name==d.target.__data__.name){
                                d3.select(".circle"+edges[i].source.index).attr("r","20");
                                d3.select(".circle"+edges[i].target.index).attr("r","13");
                                d3.select(".links"+edges[i].index).attr("stroke-width","5")
                            }
                            if(edges[i].target.name==d.target.__data__.name){
                                d3.select(".circle"+edges[i].source.index).attr("r","13");
                                d3.select(".circle"+edges[i].target.index).attr("r","20");
                                d3.select(".links"+edges[i].index).attr("stroke-width","5");
                            }
                        }
                        
                    })
                    .on("mouseout",function(d){
                        //
                        //
                        d3.select(".text"+d.target.__data__.index).attr("visibility","hidden");
                        for(var i = 0;i<edges.length;i++){
                            //
                            if(edges[i].source.name==d.target.__data__.name||edges[i].target.name==d.target.__data__.name){
                                d3.select(".circle"+edges[i].source.index).attr("r",function(d){
                                    return 5+d.links/50;
                                });
                                d3.select(".circle"+edges[i].target.index).attr("r",function(d){
                                    return 5+d.links/50;
                                });
                                d3.select(".links"+edges[i].index).attr("stroke-width","1");
                            }
                        }  
                    })
                    .call(drag);
                //
                var legends = [];
                for(var i = 0;i<maketypes.length;i++){
                    legends.push({maketype:maketypes[i]});
                }
                /* var legend = svg.selectAll(".legend")
                    .data(legends)
                    .enter()
                    .append("g")
                    .attr("class","legend")
                    .attr("transform",function(d,i){
                        return "translate(-50,"+(i*20)+")";
                    })
                    //relationrelation--
                    .on("mouseover",function(d){
                        //
                        var theMakeType = d.srcElement.__data__.maketype;
                        //
                        //
                        for(var i = 0;i<edges.length;i++){
                            d3.select(".circle"+edges[i].source.index).attr("visibility","hidden");
                            d3.select(".circle"+edges[i].target.index).attr("visibility","hidden");
                            d3.select(".links"+edges[i].index).attr("visibility","hidden");
                        }
                        for(var i = 0;i<edges.length;i++){
                            if(edges[i].source.maketype==theMakeType||edges[i].target.maketype==theMakeType){
                                d3.select(".circle"+edges[i].source.index).attr("visibility","visible");
                                d3.select(".circle"+edges[i].target.index).attr("visibility","visible");
                                d3.select(".links"+edges[i].index).attr("visibility","visible");
                                if(edges[i].source.maketype!=theMakeType){
                                    d3.select(".circle"+edges[i].source.index).style("opacity",0.2); //
                                }
                                if(edges[i].target.maketype!=theMakeType){
                                    d3.select(".circle"+edges[i].target.index).style("opacity",0.2);
                                }
                            }
                        }
                    })
                    .on("mouseout",function(d){
                        //
                        //
                        for(var i = 0;i<nodes.length;i++)
                            d3.select(".circle"+nodes[i].index).attr("visibility","visible");
                        for(var i = 0;i<edges.length;i++){
                            d3.select(".links"+edges[i].index).attr("visibility","visible");
                        }
                        //
                        var theMakeType = d.srcElement.__data__.maketype;
                        for(var i = 0;i<edges.length;i++){
                            if(edges[i].source.maketype==theMakeType&&edges[i].target.maketype==theMakeType){
                                if(edges[i].source.maketype!=theMakeType){
                                    d3.select(".circle"+edges[i].source.index).style("opacity",0.5);
                                }
                                if(edges[i].target.maketype!=theMakeType){
                                    d3.select(".circle"+edges[i].target.index).tyle("opacity",0.5);
                                }
                            }
                        }
                    });
                //
                legend.append("rect")
                    .attr("x",width*0.9)
                    .attr("y",208)
                    .attr("width",30)
                    .attr("height",10)
                    .attr("fill",function(d){
                        return color(maketypes.indexOf(d.maketype));
                    });
                //
                legend.append("text")
                    .attr("x",width*0.85)
                    .attr("y",220)
                    .attr("font-size",10)
                    .style("text-anchor","end")
                    .text(function(d){
                        return d.maketype;
                    }); */



                function ticked()
                {
                    // nodes.forEach(function (d) {
                    //     if (d.x >= width) {
                    //         d.x = width
                    //     }
                    //     if (d.y >= height) {
                    //         d.y = height
                    //     }
                    //     if (d.x <= 5) {
                    //         d.x = 5
                    //     }
                    //     if (d.y <= 5) {
                    //         d.y = 5
                    //     }
                    // })

                    links
                        .attr("x1",function(d){return d.source.x;})
                        .attr("y1",function(d){return d.source.y;})
                        .attr("x2",function(d){return d.target.x;})
                        .attr("y2",function(d){return d.target.y;});
                    
                    gs
                        .attr("transform",function(d) { return "translate(" + d.x + "," + d.y + ")"; });
                }
                //
                function started(d) {
                    if (!d.active) {
                        forceSimulation.alphaTarget(0.8).restart();
                    }
                    d.subject.fx = d.subject.x;
                    d.subject.fy = d.subject.y;
                }
                function dragged(d) {
                    d.subject.fx = d.x;
                    d.subject.fy = d.y;
                }
                function ended(d) {
                    if (!d.active) {
                        forceSimulation.alphaTarget(0);
                    }
                    d.subject.fx = null;
                    d.subject.fy = null;
                }
            })
        }
    }
</script>